.wrapper {
    display: flex;
}

.account {
    margin-left: 16px;
    display: flex;
    background-image: linear-gradient(to right bottom, red 45%, black);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;

    & .image {
        background-image: linear-gradient(to right bottom, transparent, rgba(0, 0, 0, 0.1));
        border-radius: 50%;
        height: 64px;
        width: 64px;
        object-fit: cover;
        padding: 4px;
    }

    & .name {
        text-transform: uppercase;
    }

    & .nickname {
        font-style: italic;
    }

    & .left {
        margin-left: 8px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
}

.cart {
    position: relative;
    margin-left: 32px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    text-decoration: none;
    cursor: pointer;

    & span {
        font-size: 0.75rem;
    }

    & .quantity {
        position: absolute;
        background-color: red;
        color: white;
        border-radius: 50%;
        padding: 2px;
        top: 4px;
        right: -4px;
        font-size: 0.75rem;
    }
}
