.wrapper {
    width: 320px;
    background-color: transparent;
    margin: 16px;
}

.title {
    text-transform: uppercase;
    text-align: center;
    padding: 16px;
    background-image: linear-gradient(to right bottom, transparent, red);
    color: white;
}

.list {
    background-image: linear-gradient(to right top, rgb(255, 0, 0, 0.1), transparent);
    padding: 16px 0;
}