.wrapper {
    position: relative;
    padding: 8px;
    padding-bottom: 16px;
    background-image: linear-gradient(to bottom, rgb(255, 0, 0, 0.5), rgb(255, 0, 0, 0.1));
}

.image {
    display: flex;
    width: 100%;
    height: 220px;
    object-fit: cover;
    margin-bottom: 4px;
}

.name {
    color: rgb(0, 0, 0, 0.5);
    background-color: white;
    padding: 4px;
    font-weight: lighter;
    line-height: 1;
    border: 1px solid rgba(0, 0, 0, 0.1);
    width: calc(320px - 12px * 2);

    &::before {
        content: "";
        display: inline-block;
        position: absolute;
        left: 0;
        bottom: -.5rem;
        border-top: .5rem solid rgb(0, 0, 0, 0.75);
        border-left: .5rem solid transparent;
        filter: brightness(60%);
    }    

    &::after {
        content: "";
        display: inline-block;
        position: absolute;
        right: 0;
        bottom: -.5rem;
        border-top: .5rem solid rgb(0, 0, 0, 0.75);
        border-right: .5rem solid transparent;
        filter: brightness(60%);
    }    

    &-short {
        margin-bottom: 4px;
        font-weight: bolder;
        color: rgb(0, 0, 0, 0.5);
        width: 100%;

        // hidden text line
        display: -webkit-box;
        line-height: 1.5;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.price {
    display: flex;
    justify-content: center;
    margin: 8px 0;
    color: red;
    font-size: 1.2rem;

    & .default {
        text-decoration: line-through;
        color:rgb(0, 0, 0, 0.5);
        margin: 0 8px;
    }

    & .salesOff {
        margin: 0 8px;
    }
}

.action {
    width: calc((100vw - 320px - 16px * 6) / 4);

    & .add-cart {
        width: calc(100%);
        padding: 4px;
        border:none;
        color: white;
        background-color: orangered;
        text-transform: uppercase;
        cursor: pointer;
    }
}