@import 'normalize.css';

:root {
    --primary: red;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

a[href] {
    text-decoration: unset;
}

// 
html {

    overflow: overlay;

    &::-webkit-scrollbar {
        border-radius: 0;
        width: 8px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 4px;
        background-color: rgba(0, 0, 0, 0.15);
    }

    ::-webkit-scrollbar-track {
        border-radius: 0;
        background-color: rgba(0, 0, 0, 0);
    }
}